import { useState, useEffect } from 'react';
import axios from './Api/axios';
import { Legend, LineChart, Line, Tooltip, XAxis, YAxis, ResponsiveContainer } from 'recharts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChartLine } from '@fortawesome/free-solid-svg-icons';

const MyLineChart = () => {

    const [catCl, setCatCl] = useState('telegram_channel');
    const [lineData, setLineData] = useState([]);
    const token = localStorage.getItem('access');

    function turnToTelegramChannel() {
        setCatCl('telegram_channel');
    }

    function turnToTelegramGroups() {
        setCatCl('telegram_groups');
    }

    function turnToInsta() {
        setCatCl('instagram');
    }

    function turnToTweeter() {
        setCatCl('twitter');
    }

    function turnToPost() {
        setCatCl('news_agency');
    }

    const actFilter = {
        backgroundColor: "rgb(1, 31, 75)",
        color: "white",
        borderLeftColor: "rgb(1, 31, 75)",
    };
    const dActFilter = {
    }

    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getPie = async () => {
            try {
                const response = await axios.get(`/${catCl}/get_statistics`,
                    {
                        headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' }
                    },
                    {
                        signal: controller.signal
                    });
                isMounted && setLineData(response?.data);
            } catch (err) {
                console.error(err);
            }
        }

        getPie();

        return () => {
            isMounted = false;
            isMounted && controller.abort();
        }
    }, [catCl])

    const [typeShow, setTypeShow] = useState()

    return (
        <>
            <h5 className='mt-8 px-4'><span className='inline-block place-content-center align-top text-middle-blue text-xl ml-2'><FontAwesomeIcon icon={faChartLine} /></span>نمودار حجم خبر</h5>
            <div className='psyc-container bg-white border-slate-400 rounded-md p-4 mt-4'>
                <nav className='relative rounded-lg max-w-fit h-10 mt-2 md:mb-10' style={{ zIndex: 100000000 }}>
                    <ul className='flex flex-col md:flex-row p-0 m-0'>
                        <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 bg-dark-blue md:rounded-r-md select-none' onClick={() => { setTypeShow(!typeShow) }}>انتخاب بستر</li>
                        <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_channel" ? actFilter : dActFilter} onClick={turnToTelegramChannel}>کانال‌های تلگرام</li>
                        <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_groups" ? actFilter : dActFilter} onClick={turnToTelegramGroups}>گروه‌های تلگرام</li>
                        <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "twitter" ? actFilter : dActFilter} onClick={turnToTweeter}>توییتر</li>
                        <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "instagram" ? actFilter : dActFilter} onClick={turnToInsta}>اینستاگرام</li>
                        <li className={`flex relative justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"} md:rounded-l-md`} style={catCl === "news_agency" ? actFilter : dActFilter} onClick={turnToPost}>خبرگذاری‌ها</li>
                    </ul>
                </nav>

                <ResponsiveContainer width="100%" aspect={4}>
                    <LineChart
                        width={730}
                        height={250}
                        data={lineData}
                        margin={{
                            top: 5,
                            right: 30,
                            left: 20,
                            bottom: 5,
                        }}
                    >
                        <XAxis dataKey="name" tickMargin={30} />
                        <YAxis tickMargin={50} />
                        <Tooltip coordinate={{ x: 0, y: 0 }} />
                        <Legend align='center' className='text-sm' wrapperStyle={{ 'marginBottom': '-20px' }} />
                        <Line type="monotone" dataKey="value" stroke="#8884d8" />
                    </LineChart>
                </ResponsiveContainer>
            </div>
        </>
    )
}

export default MyLineChart